import React, { useState } from "react";
import "./App.css";
import Nav from "../components/Nav";
import Cards from "../components/Cards";
import About from "../components/About";
import Ciudad from "../components/Ciudad";
import { Route } from "react-router-dom";

export default function App() {
  // espacio para codigo - React.useState()
  // estado para mantener un arreglo de ciudades
  const apiKey = "9f9f3f739d45de984b26d3e9d1a527c2";
  const [ciudades, setCiudades] = useState([]);

  function onSearch(ciudad) {
    fetch(
      `https://api.openweathermap.org/data/2.5/weather?q=${ciudad}&appid=${apiKey}&units=metric`
    )
    .then((respuesta) => respuesta.json())
    .then((respuesta) => {
      if (respuesta.main !== undefined) {
        const ciudad = {
          min: Math.round(respuesta.main.temp_min),
          max: Math.round(respuesta.main.temp_max),
          img: respuesta.weather[0].icon,
          id: respuesta.id,
          wind: respuesta.wind.speed,
          temp: respuesta.main.temp,
          name: respuesta.name,
          weather: respuesta.weather[0].main,
          clouds: respuesta.clouds.all,
          latitud: respuesta.coord.lat,
          longitud: respuesta.coord.lon,
        };
        const oldCity = ciudades.find(ciudadIngresada => ciudadIngresada.id === ciudad.id)
        if(!oldCity) {
            setCiudades(oldCities => [...oldCities, ciudad]);
        }
        else {alert("La ciudad ingresada ya se encuentra agregada")}
      } 
      else if(!respuesta.data){
        alert("Ciudad no encontrada");
      }
    })
    // .catch((err) => {
    //     alert('ciudad no encontrada')
    // })
  }

  function onFilter(ciudadId){
    let ciudad = ciudades.filter((c) => c.id === parseInt(ciudadId))

    if(ciudad.length>0){
        return ciudad[0]
    }
    else return null;
  }

  function onClose(id) {
    setCiudades((estadoAnterior) =>
      estadoAnterior.filter((el) => el.id !== id)
    );
  }

  return (
    <div className="App">
    <Route path='/2nd'>
      <Nav onSearch={onSearch} />
    </Route>
    <Route exact path='/2nd/about'
        component={About}
    />
    <Route
        exact 
        path='/'
        render={() => <Cards cities={ciudades} onClose={onClose} />}
    />
    {/* <Route component={Ciudad}/> */}
    <Route exact path='/2nd/ciudad/:id'>
        <Ciudad onFilter={onFilter}/>
    </Route>
    </div>
  );
}